import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import {Geolocation} from "@ionic-native/geolocation";

export enum EmergencyState {CALL,WAIT};

@Component({
  selector: 'page-wait',
  templateUrl: 'wait.html'
})

export class WaitPage {
  emergencyEnum = EmergencyState;
  state : EmergencyState;
  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public geolocation: Geolocation, ) {
    this.state = EmergencyState.CALL;
  }

  public startEmergency(): void {
    let confirm = this.alertCtrl.create({
      title: 'Emergency',
      message: 'Please confirm the emergency.',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
            this.emergencyOver()
          }
        },
        {
          text: 'Confirm Emergency',
          handler: () => {
            this.confirmEmergency();

          }
        }
      ],
      cssClass: 'big-alert'
    });
    confirm.present();
  }

  private confirmEmergency(): void {
    this.state = EmergencyState.WAIT;
    console.log("HELLO")
    this.geolocation.getCurrentPosition().then((resp) => {
      document.getElementById("info").innerText = resp.coords.longitude + "," + resp.coords.latitude;
    }).catch((error) => {
      document.getElementById("info").innerText = error.message;
    });
  }

  public emergencyOver(): void {
    this.state = EmergencyState.CALL;
    this.navCtrl.parent.select(0);
  }

}



